
class mixin {
	constructor(){
		this.methods = {
			base(){
                let font_size = parseFloat(getComputedStyle(window.document.documentElement)['font-size'])
				return {
                    grid: {
                        left: '12%', //
                        right: '5%', //
                        top: '18%' //
                    },
                    tooltip: {},
                    // legend: {
                    //     data: ['']
                    // },
                    dataZoom: [{  //
                        type: 'inside',
                        throttle:0,
                        // startValue: 0,
                        // endValue: 6,
                    }],
                    title:{
                        text:'',
                        show:true,
                        textStyle:{
                            fontSize:0.42*font_size,
                            fontWeight:'normal',
                        },
                        textColor:'red',
                        left:'center'
                    },
                    xAxis: {
                        splitLine: { show: false },  //
                        axisLine: {
                            lineStyle: {
                                type: 'solid',
                                color: '#000',
                                width: '1'
                            }
                        },
                        axisLabel: { //
                            show: true,
                            textStyle: {
                                color: '#999999',
                                fontSize: 0.36 * this.font_size
                            },
                        },
                        axisTick: {  //
                            show: false
                        }, 
                    },
                    yAxis: {
                        type: 'value',
                        splitLine: { show: false },  //
                        axisLine: {   //
                            lineStyle: {
                                type: 'solid',
                                color: '#000',
                                width: '1'
                            }
                        },
                        axisLabel: { //
                            show: true,
                            textStyle: {
                                color: '#999999',
                                fontSize: 0.36 * this.font_size
                            }
                        },
                        axisTick: {  //
                            show: false
                        }
                    },
                    series: [{
                        name: '',
                        type: 'line',
                        color: "#FF5B2E",
                        // symbol:'circel',      //
                        symbolSize: 0.25 * this.font_size,//
                        showSymbol:'false',
                        showAllSymbol:false,
                        itemStyle: {  //
                            normal: {
                                lineStyle: {
                                    width: 0.1*this.font_size
                                },
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position: 'top',
                                color: 'white',
                                fontSize: 0.36*font_size
                            }
                        },
                        barWidth:0.3*font_size,
                        smooth: true, //true false
                    }],
                    
                };
			},
		}
	}
}



export default new mixin();



