
class mixin {
	constructor(){
		this.methods = {
			base(){
				return {
					chart:{ // 
						// marginTop: '70',  // 
			            // marginLeft: '60',  // 
			            // marginRight:'30' ,  // 
						// marginBottom:'50' ,  // 
						marginTop: '25',  // 
						// marginLeft: '40',  // 
						// marginRight:'0' ,  // 
						marginBottom:'65' ,  // 
			            backgroundColor: 'transparent',	//
					},
					title: { //
						text: '',
			            y:5, 
			            style:{ // 
			            	fontSize:16,
			            	color:'#333'
			            }
			        },
			        credits: { //
			            enabled: false  
			        },
			        legend: { //
						enabled: true,
						align: 'center',
	        			verticalAlign: 'top',
						layout: 'horizontal', // 
			            floating: false,   // 
			            squareSymbol: true,
			            symbolRadius: 0, //
			            itemStyle: { // 
			            	fontSize: 14,
			            	color: '#999999',
			            }
			        },
			        xAxis: { //x
						crosshair: true,  // 
			            tickWidth: 0, // X
			            labels: {
							style: {
								color: '#999999',
								fontSize: 14
							}
						},
						// min:0, //
        				// max:6
			        },
			        yAxis: { //y
						min: 0, // y0
			            gridZIndex: -1,
			            lineWidth: 1,
			            title: { // y      
			                text: '',
			                align: 'high',
			                rotation: 0,
			                offset: 0,
			                y: -20,
			                x: 0,
			                style:{ // 
				            	fontSize: 12
				            }
			            },
			            labels: {
							style: {
								color: '#999999',
								fontSize:12
							}
						}
			        },
			        plotOptions: {
				        series: {
				            borderWidth:0,
				            dataLabels: {
								enabled: true,
								style: {
									fontWeight: 'none',
									color: '#FFFFFF',
									textOutline: "0px 0px contrast"
								}
							}
				        }
				    },
					series: [],
					scrollbar: {
						enabled: true
					},
				};
			},
			lineChart(){	//
				return{
					
				}
			},
			columnChart(){	//
				return {
					
				}
			}
		}
	}
}



export default new mixin();



