<template>
    <div class="echarts"></div>
</template>

<script>
    // 
    let echarts = require('echarts')
    // 
    require('echarts/lib/chart/bar')
    // title
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')
    import chartCommon from '../mixins/common.js'

    export default {
        mixins: [chartCommon],
        name: 'e-chart',
        data() {
            return {
                font_size: '',
            }
        },
        props:['option'],
        methods: {
            deepObjectMerge(FirstOBJ, SecondOBJ) { // 
                for (var key in SecondOBJ) {
                    FirstOBJ[key] = FirstOBJ[key] && FirstOBJ[key].toString() === "[object Object]" ?
                        this.deepObjectMerge(FirstOBJ[key], SecondOBJ[key]) : FirstOBJ[key] = SecondOBJ[key];
                }
                return FirstOBJ;
            },
            chartDraw() {
                let echart = echarts.init(this.$el)
                let option = this.deepObjectMerge(this.base(),this.option)
                // let option = {
                //     grid: {
                //         left: '10%', //
                //         right: '5%', //
                //         bottom: '11%',//
                //         top: '5%' //
                //     },
                //     tooltip: {},
                //     // legend: {
                //     //     data: ['']
                //     // },
                //     dataZoom: [{  //
                //         type: 'inside',
                //         throttle: '0',
                //         startValue: 0,
                //         endValue: 6,
                //     }],
                //     xAxis: {
                //         boundaryGap: false,
                //         data: this.xData,
                //         axisLine: {
                //             lineStyle: {
                //                 type: 'solid',
                //                 color: '#000',
                //                 width: '1'
                //             }
                //         },
                //         axisLabel: { //
                //             show: true,
                //             textStyle: {
                //                 color: '#999999',
                //                 fontSize: 0.36 * this.font_size
                //             }
                //         },
                //         axisTick: {  //    
                //             show: false
                //         }
                //     },
                //     yAxis: {
                //         type: 'value',
                //         splitLine: { show: false },  //
                //         axisLine: {   //
                //             lineStyle: {
                //                 type: 'solid',
                //                 color: '#000',
                //                 width: '1'
                //             }
                //         },
                //         axisLabel: { //
                //             show: true,
                //             textStyle: {
                //                 color: '#999999',
                //                 fontSize: 0.36 * this.font_size
                //             }
                //         },
                //         axisTick: {  //
                //             show: false
                //         }
                //     },
                //     series: [{
                //         name: '',
                //         type: 'line',
                //         color: "#FF5B2E",
                //         // symbol:'circel',      //
                //         symbolSize: 0.25 * this.font_size,//
                //         areaStyle: {
                //             normal: {
                //                 color: new echarts.graphic.LinearGradient(
                //                     0, 0, 0, 1,
                //                     [
                //                         { offset: 0, color: '#FF5B2E' },
                //                         { offset: 0.6, color: '#582B27' },
                //                         { offset: 1, color: 'transparent' }
                //                     ]
                //                 )

                //             }
                //         },
                //         itemStyle: {  //
                //             normal: {
                //                 lineStyle: {
                //                     width: 0.1 * this.font_size
                //                 }
                //             }
                //         },
                //         smooth: true, //true false
                //         data: this.yData
                //     }]
                // };
                echart.setOption(option)
            }
        },
        mounted() {
            this.font_size = parseFloat(getComputedStyle(window.document.documentElement)['font-size'])
            this.chartDraw()
            let _this = this
            // setInterval(function(){
            //     _this.yData = []
            //     for(var i=0;i<7;i++){
            //         _this.yData.push(Math.ceil(Math.random()*1000))
            //     }
            //     _this.chartDraw()
            // },1000)
        },
        watch:{
            option:{
                handler (val, oldVal) {
                    this.chartDraw()
                },
                deep:true
            }
        }
    }
</script>

<style scoped lang="scss">
    .echarts {
        width: 100%;
        height: 100%
    }
</style>


